import React from 'react';
import AWS from 'aws-sdk';

// redux
import { useDispatch, useSelector } from 'react-redux';
import { imageCreators } from '../redux/modules/image';
import { postActions } from '../redux/modules/post';

// m-ui...
import { Text, Image, Container, Buttons } from '../elements/index';
import { Grid, TextField } from '@mui/material';
import InputLabel from '@mui/material/InputLabel';
import MenuItem from '@mui/material/MenuItem';
import FormControl from '@mui/material/FormControl';
import Select from '@mui/material/Select';
import Box from '@mui/material/Box';
import styled from 'styled-components';

// calendar...
import 'react-datepicker/dist/react-datepicker.css';
import { createTheme, ThemeProvider } from '@mui/material/styles';
import Editor from '../components/Posting/Editor';
import DatePicker from 'react-datepicker';
import { ko } from 'date-fns/esm/locale';

const Posting = () => {
  const dispatch = useDispatch();
  const preview = useSelector((state) => state.image.preview);
  const [title, setTitle] = React.useState('');
  const [content, setContent] = React.useState('');
  const [rundate, setRundate] = React.useState(new Date());
  const newsDate = new Date(
    rundate.getTime() - rundate.getTimezoneOffset() * 160000,
  );
  const [startdate, setStartdate] = React.useState(new Date(newsDate));
  const [enddate, setEnddate] = React.useState(new Date(newsDate));
  const [srundate, setSRundate] = React.useState(new Date());
  const [sstartdate, setSStartdate] = React.useState(new Date());
  const [senddate, setSEnddate] = React.useState(new Date());
  const [lrundate, setLRundate] = React.useState(new Date());
  const [location, setLocation] = React.useState('');
  const [type, setType] = React.useState('');
  const [distance, setDistance] = React.useState('');
  const [limit, setLimit] = React.useState('');
  const [intro, setIntro] = React.useState('');

  const contents = {
    title: title,
    content: content,
    runningDate: rundate,
    startDate: startdate,
    endDate: enddate,
    location: location,
    type: type,
    distance: distance,
    limitPeople: limit,
    crewHeadIntro: intro,
  };

  const handleRunDate = (date) => {
    const newDate = new Date(date.getTime() - date.getTimezoneOffset() * 60000);
    setRundate(newDate);
  };

  const handleStartDate = (date) => {
    const newDate = new Date(date.getTime() - date.getTimezoneOffset() * 60000);
    setStartdate(newDate);
  };

  const handleEndDate = (date) => {
    const newDate = new Date(date.getTime() - date.getTimezoneOffset() * 60000);
    setEnddate(newDate);
  };

  const handleSRunDate = (date) => {
    setSRundate(date);
  };

  const handleLRunDate = (date) => {
    const newDate = new Date(
      date.getTime() + date.getTimezoneOffset() * 160000,
    );
    setLRundate(newDate);
  };

  const ldate = new Date();
  const limDate = new Date(
    ldate.getTime() - ldate.getTimezoneOffset() * 160000,
  ); 

  const handleSStartDate = (date) => {
    setSStartdate(date);
  };

  const handleSEndDate = (date) => {
    setSEnddate(date);
  };

  const handleLocation = (e) => {
    setLocation(e.target.value);
  };

  const handleLimit = (e) => {
    setLimit(e.target.value);
  };

  const handleType = (e) => {
    setType(e.target.value);
  };

  const handleDistance = (e) => {
    setDistance(e.target.value);
  };

  const getEditorContent = (content) => {
    setContent(content);
  };

  const inp = document?.getElementById('inputbutton');

  const fileInputClick = () => {
    inp?.current?.dispatchEvent(new Event('click'));
  };

  AWS.config.update({
    region: 'ap-northeast-2', //     . (Ex. "ap-northeast-2")
    credentials: new AWS.CognitoIdentityCredentials({
      IdentityPoolId: 'ap-northeast-2:84ac387b-b3ed-4d45-8353-7ed4b6dd44aa', // cognito      . (Ex. "ap-northeast-2...")
    }),
  });

  const fileInput = React.useRef();

  //  
  const filePreview = () => {
    const reader = new FileReader();
    const file = fileInput.current.files[0];
    // 
    reader.readAsDataURL(file);
    // 
    reader.onloadend = () => {
      dispatch(imageCreators.setPreview(reader.result));
    };
  };

  const uploadFile = () => {
    //      -  +  
    const date = new Date();
    // input     
    const file = fileInput.current.files[0];

    // S3  
    const S3_BUCKET = 'jupgging-image';

    if (!file) {
      window.alert('  !');
      return;
    }
    if (
      contents.title === '' ||
      contents.content === '' ||
      contents.runningDate === '' ||
      contents.startDate === '' ||
      contents.endDate === '' ||
      contents.location === '' ||
      contents.type === '' ||
      contents.distance === '' ||
      contents.limitPeople === '' ||
      contents.crewHeadIntro === ''
    ) {
      window.alert('  !');
      return;
    }
    if (
      contents.title.length > 14 
    ) {
      window.alert('   !');
      return;
    }

    // S3 SDK   
    const upload = new AWS.S3.ManagedUpload({
      params: {
        Bucket: S3_BUCKET, //   
        Key: file.name + date.getTime() + '.jpg', //   (*   !)
        Body: file, //   
      },
    });
    const promise = upload.promise();
    promise.then(
      function (data) {
        dispatch(imageCreators.imageUpload(data.Location));
        const content = {
          ...contents,
          postImg: data.Location,
        };
        dispatch(postActions.addPostDB(content));
      },
      function (err) {
        return alert(' : ', err.msg);
      },
    );
  };

  const inputTheme = createTheme({
    shape: {
      borderRadius: 10,
    },
    palette: {
      primary: { main: '#23C8AF' },
    },
  });

  return (
    <React.Fragment>
      <Container width="700px">
        <Grid width="700px" margin="auto" padding="10px">
          <Text align="center" size="32px">
            <h4> </h4>
          </Text>
          <Grid container spacing={3}>
            <Grid item xs={12} sm={2}>
              <Text size="18px" padding="17px 0px 0px 0px" bold>
                
              </Text>
            </Grid>
            <Grid
              container
              direction="row"
              justifyContent="flex-start"
              alignItems="flex-start"
              item
              xs={12}
              sm={10}
            >
              <ThemeProvider theme={inputTheme}>
                <TextField
                  required
                  id="outlined-required"
                  label="    !"
                  fullWidth
                  value={title}
                  onChange={(e) => {
                    setTitle(e.target.value);
                  }}
                  error={title.length < 5 && title.length >= 1 || title.length > 16 }
                  helperText={
                    title.length < 5 && title.length >= 1 || title.length > 16 
                      ? '   5 ,  14  .'
                      : ''
                  }
                />
              </ThemeProvider>
            </Grid>
            <Grid item xs={12} sm={2}>
              <Text size="18px" padding="17px 0px 0px 0px" bold>
                
              </Text>
            </Grid>
            <Grid item xs={12} sm={10}>
              <RunDatePicker
                portalId="root-portal"
                selected={srundate <= ldate ? limDate : srundate}
                onChange={(date) => {
                  handleRunDate(date);
                  handleSRunDate(date);
                  handleLRunDate(date);
                }}
                showMonthDropdown
                locale={ko}
                showTimeSelect
                timeFormat="HH:mm"
                timeIntervals={30}
                dateFormatCalendar="yyyy MMMM"
                timeCaption=""
                dateFormat="yyyy MM d aa h:mm"
                minDate={limDate}
                popperModifiers={{
                  preventOverflow: { enable: true },
                }}
                fixedHeight
              />
            </Grid>
            <Grid item xs={12} sm={2}>
              <Text size="18px" padding="17px 0px 0px 0px" bold>
                
              </Text>
            </Grid>
            <Grid item xs={12} sm={4}>
              <StartDatePicker
                portalId="root-portal"
                locale={ko}
                selected={sstartdate}
                onChange={(date) => {
                  handleStartDate(date);
                  handleSStartDate(date);
                }}
                dateFormatCalendar="yyyy MMMM"
                selectsStart
                dateFormat="yyyy MM dd"
                startDate={sstartdate}
                minDate={new Date()}
                maxDate={lrundate}
                endDate={enddate}
                fixedHeight
              />
            </Grid>
            <Grid item xs={12} sm={1}>
              <Text
                size="18px"
                padding="17px 0px 0px 0px"
                margin="0px -55px 0px 0px"
                align="center"
              >
                ~
              </Text>
            </Grid>
            <Grid item xs={12} sm={5}>
              <EndDatePicker
                portalId="root-portal"
                locale={ko}
                selected={senddate}
                onChange={(date) => {
                  handleEndDate(date);
                  handleSEndDate(date);
                }}
                fixedHeight
                dateFormatCalendar="yyyy MMMM"
                selectsEnd
                dateFormat="yyyy MM dd"
                startDate={sstartdate}
                endDate={enddate}
                minDate={sstartdate}
                maxDate={lrundate}
              />
            </Grid>
            <Grid item xs={12} sm={2}>
              <Text size="18px" padding="17px 0px 0px 0px" bold>
                
              </Text>
            </Grid>
            <Grid item xs={12} sm={4}>
              <ThemeProvider theme={inputTheme}>
                <Box
                  component="form"
                  sx={{
                    '& .MuiTextField-root': { width: '220px' },
                  }}
                  noValidate
                  autoComplete="off"
                >
                  <TextField
                    id="outlined-read-only-input"
                    defaultValue=""
                    InputProps={{
                      readOnly: true,
                    }}
                  />
                </Box>
              </ThemeProvider>
            </Grid>
            <Grid item xs={12} sm={6}>
              <ThemeProvider theme={inputTheme}>
                <Box
                  component="form"
                  sx={{
                    '& .MuiInputBase-root': { width: '220px', left: '107px' },
                    '& .MuiInputLabel-root': { left: '107px' },
                  }}
                  noValidate
                  autoComplete="off"
                >
                  <FormControl sx={{ minWidth: 120 }}>
                    <InputLabel id="demo-simple-select-helper-label">
                       
                    </InputLabel>
                    <Select
                      labelId="demo-simple-select-helper-label"
                      id="demo-simple-select-helper"
                      value={location}
                      label=" "
                      onChange={handleLocation}
                      required
                    >
                      <MenuItem value={''}></MenuItem>
                      <MenuItem value={''}></MenuItem>
                      <MenuItem value={''}></MenuItem>
                      <MenuItem value={''}></MenuItem>
                      <MenuItem value={''}></MenuItem>
                      <MenuItem value={''}></MenuItem>
                      <MenuItem value={''}></MenuItem>
                      <MenuItem value={''}></MenuItem>
                      <MenuItem value={''}></MenuItem>
                      <MenuItem value={''}></MenuItem>
                      <MenuItem value={''}></MenuItem>
                      <MenuItem value={''}></MenuItem>
                      <MenuItem value={''}></MenuItem>
                      <MenuItem value={''}></MenuItem>
                      <MenuItem value={''}></MenuItem>
                      <MenuItem value={''}></MenuItem>
                      <MenuItem value={''}></MenuItem>
                      <MenuItem value={''}></MenuItem>
                      <MenuItem value={''}></MenuItem>
                      <MenuItem value={''}></MenuItem>
                      <MenuItem value={''}></MenuItem>
                      <MenuItem value={''}></MenuItem>
                      <MenuItem value={''}></MenuItem>
                      <MenuItem value={''}></MenuItem>
                      <MenuItem value={''}></MenuItem>
                    </Select>
                  </FormControl>
                </Box>
              </ThemeProvider>
            </Grid>
            <Grid item xs={12} sm={2}>
              <Text size="18px" padding="17px 0px 0px 0px" bold>
                
              </Text>
            </Grid>
            <Grid item xs={12} sm={10}>
              <ThemeProvider theme={inputTheme}>
                <Box
                  component="form"
                  sx={{
                    '& .MuiSelect-root': { width: '220px' },
                  }}
                  noValidate
                  autoComplete="off"
                >
                  <FormControl sx={{ minWidth: 120 }}>
                    <InputLabel id="demo-simple-select-helper-label">
                       
                    </InputLabel>
                    <Select
                      labelId="demo-simple-select-helper-label"
                      id="demo-simple-select-helper"
                      value={type}
                      label=" "
                      onChange={handleType}
                      required
                    >
                      <MenuItem value={'()'}>()</MenuItem>
                      <MenuItem value={''}></MenuItem>
                      <MenuItem value={''}></MenuItem>
                      <MenuItem value={'  '}>  </MenuItem>
                    </Select>
                  </FormControl>
                </Box>
              </ThemeProvider>
            </Grid>
            <Grid item xs={12} sm={2}>
              <Text size="18px" padding="17px 0px 0px 0px" bold>
                
              </Text>
            </Grid>
            <Grid item xs={12} sm={10}>
              <ThemeProvider theme={inputTheme}>
                <Box
                  component="form"
                  sx={{
                    '& .MuiSelect-root': { width: '220px' },
                  }}
                  noValidate
                  autoComplete="off"
                >
                  <FormControl sx={{ minWidth: 120 }}>
                    <InputLabel id="demo-simple-select-helper-label">
                       
                    </InputLabel>
                    <Select
                      labelId="demo-simple-select-helper-label"
                      id="demo-simple-select-helper"
                      value={distance}
                      label=" "
                      onChange={handleDistance}
                      required
                    >
                      <MenuItem value={'1km '}>1km </MenuItem>
                      <MenuItem value={'1km~3km'}>1km~3km</MenuItem>
                      <MenuItem value={'3km~5km'}>3km~5km</MenuItem>
                      <MenuItem value={'5km '}>5km </MenuItem>
                    </Select>
                  </FormControl>
                </Box>
              </ThemeProvider>
            </Grid>
            <Grid item xs={12} sm={2}>
              <Text size="18px" padding="17px 0px 0px 0px" bold>
                
              </Text>
            </Grid>
            <Grid item xs={12} sm={10}>
              <ThemeProvider theme={inputTheme}>
                <Box
                  component="form"
                  sx={{
                    '& .MuiSelect-root': { width: '220px' },
                  }}
                  noValidate
                  autoComplete="off"
                >
                  <FormControl sx={{ minWidth: 200 }}>
                    <InputLabel id="demo-simple-select-helper-label">
                       3
                    </InputLabel>
                    <Select
                      labelId="demo-simple-select-helper-label"
                      id="demo-simple-select-helper"
                      value={limit}
                      label=" "
                      onChange={handleLimit}
                      required
                    >
                      <MenuItem value={3}>3</MenuItem>
                      <MenuItem value={4}>4</MenuItem>
                      <MenuItem value={5}>5</MenuItem>
                      <MenuItem value={6}>6</MenuItem>
                      <MenuItem value={7}>7</MenuItem>
                      <MenuItem value={8}>8</MenuItem>
                      <MenuItem value={9}>9</MenuItem>
                      <MenuItem value={10}>10</MenuItem>
                    </Select>
                  </FormControl>
                </Box>
              </ThemeProvider>
            </Grid>
            <Grid item xs={12} sm={2}>
              <Text size="18px" padding="17px 0px 0px 0px" bold>
                
              </Text>
            </Grid>
            <ThemeProvider theme={inputTheme}>
              <Grid item xs={12} sm={10}>
                <Box
                  component="form"
                  sx={{
                    '& .MuiTextField-root': { width: '100%' },
                  }}
                  noValidate
                  autoComplete="off"
                >
                  <div>
                    <TextField
                      required
                      id="outlined-textarea"
                      multiline
                      rows={6}
                      label="   .   ! (200 )"
                      value={intro}
                      onChange={(e) => {
                        setIntro(e.target.value);
                      }}
                    />
                  </div>
                </Box>
              </Grid>
            </ThemeProvider>
            <Grid item xs={12} sm={2}>
              <Text size="18px" padding="17px 0px 0px 0px" bold>
                
              </Text>
            </Grid>
            <Grid item xs={12} sm={10}>
              <Editor getEditorContent={getEditorContent} />
            </Grid>
            <Grid item xs={12} sm={2}>
              <Text size="18px" padding="17px 0px 0px 0px" bold>
                
              </Text>
            </Grid>
            <Grid item xs={12} sm={4}>
              <Image
                shape="rectangle"
                src={
                  preview
                    ? preview
                    : 'https://jupgging-image.s3.ap-northeast-2.amazonaws.com/postingdefaultimage.jpg'
                }
              />
            </Grid>
            <Grid item xs={12} sm={6} margin="auto">
              <Image
                width="200px"
                height="193px"
                padding="10px 10px"
                borderRadius="12px"
                shape="rec"
                src="https://jupgging-image.s3.ap-northeast-2.amazonaws.com/camera_input.png"
                _onClick={fileInputClick}
              />
              <Grid
              // display='none' 
              >
                <input
                  accept="image/*"
                  id="inputbutton"
                  type="file"
                  ref={fileInput}
                  onChange={filePreview}
                />
              </Grid>
            </Grid>
          </Grid>
            <Text align="center" size="14px" color="#999999" margin="30px 0px -20px 0px">
                       .
            </Text>
            <Text align="center" size="14px" color="#999999" margin="30px 0px -20px 0px">
                 ,         .
            </Text>
          <Grid container padding="50px">
            <ThemeProvider theme={inputTheme}>
              <Buttons large _onClick={uploadFile}>
                 
              </Buttons>
            </ThemeProvider>
          </Grid>
        </Grid>
      </Container>
    </React.Fragment>
  );
};

const RunDatePicker = styled(DatePicker)`
  width: 105%;
  height: 40px;
  padding: 6px 12px;
  font-size: 14px;
  text-align: center;
  border: 1px solid #acacac;
  border-radius: 10px;
  outline: none;
  cursor: pointer;
  background-color: #fff;
`;

const StartDatePicker = styled(DatePicker)`
  width: 105%;
  height: 40px;
  padding: 6px 12px;
  font-size: 14px;
  text-align: center;
  border: 1px solid #acacac;
  border-radius: 10px;
  outline: none;
  cursor: pointer;
  background-color: #fff;
`;

const EndDatePicker = styled(DatePicker)`
  width: 100%;
  height: 40px;
  padding: 6px 12px;
  font-size: 14px;
  text-align: center;
  border: 1px solid #acacac;
  border-radius: 10px;
  outline: none;
  cursor: pointer;
  background-color: #fff;
  margin: 0px -41px 0px 49px;
`;

export default Posting;
