import React from 'react';
import { Redirect } from 'react-router';
import {
  SignupForm,
  TypeCheckbox,
  DistanceCheckbox,
  LocationCheckbox,
} from '../components';

import { getsCookie } from '../shared/Cookie';

const SignUp = () => {
  const cookie = getsCookie('token');

  if (cookie == '') {
    return <Redirect to='/' />;
  }

  return (
    <React.Fragment>
      <SignupForm />
      {/*    ?
      <TypeCheckbox />
          ?   ?
      <DistanceCheckbox />
        ?
      <LocationCheckbox /> */}
    </React.Fragment>
  );
};

export default SignUp;
